.project {
  margin: 5%;
  padding: 2.5em;
  box-shadow: var(--shadow);
}

.project__title {
  margin-bottom: .2em;
}

.project__date {
  margin-bottom: 1.0em;
  font-size: .9em;
}

.project__description {
  margin-top: 1em;
}

.project__stack {
  display: flex;
  flex-wrap: wrap;
  margin: 1.2em 0;
}

.project__stack-item {
  margin: 0.5em;
  padding: 0.5em;
  font-weight: 500;
  font-size: 0.7rem;
  border-radius: 10px;
  color: var(--clr-fg-alt);
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 0px;
}

.project .project__link {
  display: block;
  margin: 3em auto;
  text-align: center;
  font-size: 1em;
  width: 200px;
}

.project .project__link:hover {
  color: var(--clr-primary);
}

.project .project__area {
  margin: 3em auto;
  position: relative;
  width: 100%;
  padding-bottom: 54%;
}

.project .project__youtube {
  position: absolute;
  width: 100%;
  height: 100%;
  border: 0;
  border-radius: 10px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}