@import url('https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap');

.about {
  flex-direction: column;
  text-align: center;
}

.about__intro {
  font-size: 38px;
  line-height: 1.5;
  margin: 20vh 20px 60vh 20px;
}

.Typewriter p {
  display: inline-block;
  font-size: 44px;
  font-weight: 550;
  color: var(--clr-primary);
}

.about__resume {
  display: inline-block;
  position: relative;
  font-family: 'Do Hyeon', sans-serif;
  top: -40vh;
  font-size: 26px;
  font-weight: 500;
  color: var(--clr-primary);
  animation: fade-in 1s;
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.about__resume:hover {
  animation: text_deco 1.2s infinite;
}

@keyframes text_deco {
  80% {
    color: white;
    text-shadow: .03em .03em 0 var(--clr-primary);
  }
}

.about__content {
  width: 100%;
}

.about__role {
  display: block;
  margin-bottom: 1.2em;
  font-size: 32px;
  font-weight: 500;
  padding: 0 20px;
}

.about__desc {
  display: inline-block;
  margin-bottom: 2.4em;
  font-size: 1.2rem;
  max-width: 618px;
  padding: 0 20px;
}

.timeline {
  max-width: 90%;
}

.timeline::before {
  left: calc(34% - 2px);
  opacity: .3;
  border-width: 2px;
  border-radius: 20px;
  color: var(--clr-fg-alt);
}

.timeline .title {
  z-index: 1;
}

.timeline div {
  line-height: 2.5em;
  color: var(--clr-fg);
}

.timeline span {
  margin: 0 5px;
  font-size: 13.5px;
  font-weight: 600;
}

.timeline p {
  font-size: 1.1em;
  font-weight: 500;
}

.timeline .timeline-item-dateinner {
  opacity: .8;
  font-size: 1.5em;
  font-weight: 500;
  background: var(--clr-primary);
  color: var(--clr-bg);
  box-shadow: var(--shadow);
}

@media (max-width: 768px) {

  .about__intro {
    font-size: 1.4em;
  }

  .Typewriter p {
    font-size: 1.3em;
  }

  .about__resume {
    font-size: 1.2em;
  }

  .about__role {
    font-size: 1.5em;
  }

  .about__desc {
    font-size: .95em;
  }

  .timeline::before {
    left: 10px;
  }
  
  .timeline .title {
    top: 8px;
    right: -2px;
    z-index: 1;
  }
}