.skills__chart {
  text-align: center;
  font-size: 1.1em;
}

.skills__chart span {
  margin: 0 3px;
}

.skills__list {
  max-width: 450px;
  width: 95%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.skills__list-item {
  margin: .5em;
  padding: .4em .5em;
  border-radius: 15px;
  transition: transform 0.1s linear;
}

.skills__list-item:hover {
  transform: translateY(-5px);
}

@media (max-width: 600px) {
  .skills__list-item {
    font-size: .9em;
  }
}
