@import url(https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Hahmlet&display=swap);
.nav__list {
  margin-right: 1.5em;
  display: flex;
}

.nav__list-item {
  margin-left: 1.5em;
}

.app .nav__hamburger {
  display: none;
}

@media (max-width: 600px) {
  .nav__list {
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    inset: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
  }

  .nav__list-item {
    margin: 0.5em 0;
  }

  .app .nav__hamburger {
    display: block;
    z-index: 2;
    margin-left: 0.8em;
  }
}

.header {
  height: 8em;
  max-width: 1100px;
  width: 95%;
  margin: 0 auto;
  justify-content: space-between;
}

.header .title {
  display: inline-block;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  font-size: 20px;
  font-weight: 400;
}

.header .title:hover {
  color: var(--clr-primary);
  -webkit-animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite
      alternate;
          animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite
      alternate;
}

@-webkit-keyframes bop {
  0% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  50%,
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes bop {
  0% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  50%,
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@media (max-width: 600px) {
  .header {
    height: 5em;
  }
}

.about {
  flex-direction: column;
  text-align: center;
}

.about__intro {
  font-size: 38px;
  line-height: 1.5;
  margin: 20vh 20px 60vh 20px;
}

.Typewriter p {
  display: inline-block;
  font-size: 44px;
  font-weight: 550;
  color: var(--clr-primary);
}

.about__resume {
  display: inline-block;
  position: relative;
  font-family: 'Do Hyeon', sans-serif;
  top: -40vh;
  font-size: 26px;
  font-weight: 500;
  color: var(--clr-primary);
  -webkit-animation: fade-in 1s;
          animation: fade-in 1s;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.about__resume:hover {
  -webkit-animation: text_deco 1.2s infinite;
          animation: text_deco 1.2s infinite;
}

@-webkit-keyframes text_deco {
  80% {
    color: white;
    text-shadow: .03em .03em 0 var(--clr-primary);
  }
}

@keyframes text_deco {
  80% {
    color: white;
    text-shadow: .03em .03em 0 var(--clr-primary);
  }
}

.about__content {
  width: 100%;
}

.about__role {
  display: block;
  margin-bottom: 1.2em;
  font-size: 32px;
  font-weight: 500;
  padding: 0 20px;
}

.about__desc {
  display: inline-block;
  margin-bottom: 2.4em;
  font-size: 1.2rem;
  max-width: 618px;
  padding: 0 20px;
}

.timeline {
  max-width: 90%;
}

.timeline::before {
  left: calc(34% - 2px);
  opacity: .3;
  border-width: 2px;
  border-radius: 20px;
  color: var(--clr-fg-alt);
}

.timeline .title {
  z-index: 1;
}

.timeline div {
  line-height: 2.5em;
  color: var(--clr-fg);
}

.timeline span {
  margin: 0 5px;
  font-size: 13.5px;
  font-weight: 600;
}

.timeline p {
  font-size: 1.1em;
  font-weight: 500;
}

.timeline .timeline-item-dateinner {
  opacity: .8;
  font-size: 1.5em;
  font-weight: 500;
  background: var(--clr-primary);
  color: var(--clr-bg);
  box-shadow: var(--shadow);
}

@media (max-width: 768px) {

  .about__intro {
    font-size: 1.4em;
  }

  .Typewriter p {
    font-size: 1.3em;
  }

  .about__resume {
    font-size: 1.2em;
  }

  .about__role {
    font-size: 1.5em;
  }

  .about__desc {
    font-size: .95em;
  }

  .timeline::before {
    left: 10px;
  }
  
  .timeline .title {
    top: 8px;
    right: -2px;
    z-index: 1;
  }
}
.skills__chart {
  text-align: center;
  font-size: 1.1em;
}

.skills__chart span {
  margin: 0 3px;
}

.skills__list {
  max-width: 450px;
  width: 95%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.skills__list-item {
  margin: .5em;
  padding: .4em .5em;
  border-radius: 15px;
  transition: -webkit-transform 0.1s linear;
  transition: transform 0.1s linear;
  transition: transform 0.1s linear, -webkit-transform 0.1s linear;
}

.skills__list-item:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

@media (max-width: 600px) {
  .skills__list-item {
    font-size: .9em;
  }
}

.project {
  margin: 5%;
  padding: 2.5em;
  box-shadow: var(--shadow);
}

.project__title {
  margin-bottom: .2em;
}

.project__date {
  margin-bottom: 1.0em;
  font-size: .9em;
}

.project__description {
  margin-top: 1em;
}

.project__stack {
  display: flex;
  flex-wrap: wrap;
  margin: 1.2em 0;
}

.project__stack-item {
  margin: 0.5em;
  padding: 0.5em;
  font-weight: 500;
  font-size: 0.7rem;
  border-radius: 10px;
  color: var(--clr-fg-alt);
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 0px;
}

.project .project__link {
  display: block;
  margin: 3em auto;
  text-align: center;
  font-size: 1em;
  width: 200px;
}

.project .project__link:hover {
  color: var(--clr-primary);
}

.project .project__area {
  margin: 3em auto;
  position: relative;
  width: 100%;
  padding-bottom: 54%;
}

.project .project__youtube {
  position: absolute;
  width: 100%;
  height: 100%;
  border: 0;
  border-radius: 10px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
.projects__grid {
  margin: 0 auto;
  max-width: 1100px;
}

.scroll-top {
  position: fixed;
  bottom: 2em;
  right: 4em;
  background-color: transparent;
}

@media (max-width: 900px) {
  .scroll-top {
    display: none;
  }
}

.footer {
  padding: 4em 0;
  margin-top: 4em;
  text-align: center;
}

.footer__link {
  font-weight: 400;
  padding: 0;
  margin: 1.2em auto;
}

@media (max-width: 600px) {
  .footer {
    padding: 3em;
    margin-top: 3em;
    font-size: .8em;
  }
}
.app {
  cursor: default;
  line-height: 1.5;
  color: var(--clr-fg);
  background-color: var(--clr-bg);
}

.light {
  --clr-bg: #fcfcfc;
  --clr-bg-alt: #fff;
  --clr-fg: #555;
  --clr-fg-alt: #444;
  --clr-primary: #2978b5;
  --shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.dark {
  --clr-bg: #23283e;
  --clr-bg-alt: #2a2f4c;
  --clr-fg: #bdbddd;
  --clr-fg-alt: #cdcdff;
  --clr-primary: #90a0d9;
  --shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

main {
  max-width: 1100px;
  width: 95%;
  margin: 0 auto;
}

.section {
  margin-top: 5em;
}

.section__title {
  text-align: center;
  text-transform: uppercase;
  margin: 2em auto;
}

.center {
  display: flex;
  align-items: center;
}

.link {
  color: var(--clr-fg);
  padding: 0 0 0.3em 0;
  position: relative;
}

.link:hover {
  color: var(--clr-primary);
}

.link::before {
  content: '';
  display: inline;
  width: 0%;
  height: 0.2em;
  position: absolute;
  bottom: 0;
  background-color: var(--clr-primary);
  transition: width 0.2s ease-in;
}

.link:hover::before,
.link:focus::before {
  width: 100%;
}

.link--nav {
  color: var(--clr-fg);
  text-transform: lowercase;
  font-weight: 500;
}

.link--icon {
  color: var(--clr-fg);
}

.btn {
  display: inline-block;
  cursor: pointer;
  padding: 0.8em 1.4em;
  font-weight: 500;
  font-size: 0.9rem;
  text-transform: lowercase;
  transition: -webkit-transform 0.1s ease-in-out;
  transition: transform 0.1s ease-in-out;
  transition: transform 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
}

.btn--outline {
  color: var(--clr-primary);
  border: 2px solid var(--clr-primary);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.btn--outline:hover,
.btn--outline:focus {
  color: var(--clr-bg);
}

.btn--outline:before {
  content: '';
  position: absolute;
  background-color: var(--clr-primary);
  right: 100%;
  bottom: 0;
  left: 0;
  top: 0;
  z-index: -1;
  transition: right 0.2s ease-in-out;
}

.btn--outline:hover:before,
.btn--outline:focus:before {
  right: 0;
}

.btn--plain {
  text-transform: none;
  text-transform: initial;
  background-color: var(--clr-bg-alt);
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 0px;
  border: 0;
}

.btn--plain:hover {
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px);
}

.btn--icon {
  padding: 0;
}

.btn--icon:hover,
.btn--icon:focus {
  color: var(--clr-primary);
}

.btn--icon:active {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

@media (max-width: 600px) {

  .section {
    margin-top: 4em;
  }

  .section__title {
    margin: 1.2em auto;
  }

}
* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  background-color: inherit;
  color: inherit;
  font-family: 'Hahmlet', serif;
  font-size: inherit;
  box-shadow: none;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

h1,
h2,
h3,
h4 {
  line-height: 1.2;
  color: var(--clr-fg-alt);
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1.3rem;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

@media (max-width: 900px) {
  h1 {
    font-size: 2.6rem;
  }

  h2 {
    font-size: 1.6rem;
  }
}

