.header {
  height: 8em;
  max-width: 1100px;
  width: 95%;
  margin: 0 auto;
  justify-content: space-between;
}

.header .title {
  display: inline-block;
  transform: scale(0.9);
  font-size: 20px;
  font-weight: 400;
}

.header .title:hover {
  color: var(--clr-primary);
  animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite
      alternate;
}

@keyframes bop {
  0% {
    transform: scale(0.9);
  }
  50%,
  100% {
    transform: scale(1);
  }
}

@media (max-width: 600px) {
  .header {
    height: 5em;
  }
}
